import React from "react";
// import axios from "axios";

export default function Modal(props) {


 
 
 

  const [items, setItems] = React.useState({
    name: "",
    email: "",
    phoneNo: "",
    identity:""
  });

  const handleSubmit = (e) => {
    console.log(items);
    setItems({
      name: "",
      email: "",
      phoneNo: "",
      identity:""
    })
    // props.deleteChange();
    // logic --
    

  };

  const handleChange = (e) => {
    console.log(e.target.value, e.target.name);
    var name = e.target.name;
    var value = e.target.value;
    setItems((prev) => {
      if (name === "Name") {
        return {
          name: value,
          email: prev.email,
          phoneNo: prev.phoneNo,
          identity:props.text
        };
      } else if (name === "email") {
        return {
          email: value,
          name: prev.name,
          phoneNo: prev.phoneNo,
          identity:props.text
        };
      } else {
        return {
          name: prev.name,
          email: prev.email,
          phoneNo: value,
          identity:props.text
        };
      }
    });

  };

  return (
    <>

      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">
                JOIN US
                <h1>{props.text}</h1>
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close" onClick={()=>{props.deleteChange()}}
              ></button>
            </div>
            {/*  modal body */}
            <div className="modal-body">
              <form>
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    required
                    placeholder="Name"
                    autoFocus
                    onChange={handleChange}
                    value={items.name}
                    name="Name"
                  ></input>
                </div>

                <div class="form-group">
                  <input
                    type="email"
                    class="form-control"
                    id="exampleInputPassword1"
                    placeholder="Email"
                    required
                    onChange={handleChange}
                    value={items.email}
                    name="email"
                  ></input>
                </div>
                <div class="form-group">
                  <input
                    type="number"
                    class="form-control"
                    placeholder="Contact Number"
                    required
                    onChange={handleChange}
                    value={items.phoneNo}
                    name="phoneNo"
                  ></input>
                </div>

                {/* <button type="submit" class="btn btn-primary">
                  Submit
                </button> */}
              </form>
            </div>

            {/* modal footer */}
            <div class="modal-footer">
              <button
                type="button"
                class="button"
                onClick={handleSubmit}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
